"use client";
import { useToast } from "@/hooks/use-toast";
import React, { useState, useEffect, useRef } from "react";
import { School2Icon, Calendar, Flame, Clock3, School, DollarSign, Sparkles, Rocket, Stars, ChevronLeft, ChevronRight } from "lucide-react";
import Image from "next/image";
import { motion, AnimatePresence } from 'framer-motion';
import EventCard from "@/components/ui/EventCard";
import { events, EventType } from '@/constants/events';
import Link from "next/link";
import bgImage from "@/public/bg-events.png";

const filters = [
    { label: "All", icon: School2Icon },
    { label: "Weekdays", icon: Calendar },
    { label: "Popular", icon: Flame },
    { label: "Latest", icon: Clock3 },
    { label: "University", icon: School },
    { label: "Price", icon: DollarSign },
];
let isStudent, isHost, isAdmin = true;

const FloatingBlobs = () => (
    <>
        {[...Array(5)].map((_, i) => (
            <motion.div
                key={i}
                initial={{ scale: 0, rotate: 0 }}
                animate={{
                    scale: [0.8, 1.2, 0.8],
                    rotate: [0, 180, 360],
                    x: Math.sin((i * Math.PI) / 2.5) * 100,
                    y: Math.cos((i * Math.PI) / 2.5) * 100,
                }}
                transition={{
                    duration: 10 + i * 2,
                    repeat: Infinity,
                    ease: "easeInOut",
                }}
                className={`absolute w-${40 + i * 20} h-${40 + i * 20} opacity-10 blur-xl
                bg-gradient-to-r ${i % 2 ? 'from-pink-400 to-purple-400' : 'from-cyan-400 to-indigo-400'}
                rounded-full`}
            />
        ))}
    </>
);

const AllEventsPage = ({ className }: React.HTMLAttributes<HTMLDivElement>) => {
    const { toast } = useToast();
    const [event, setEvents] = useState<EventType[]>([])
    const [activeFilter, setActiveFilter] = useState<'all' | 'weekdays' | 'popular' | 'latest' | 'university' | 'price'>('all');
    const [isLoading, setIsLoading] = useState(true);
    const underlineRef = useRef<HTMLDivElement | null>(null);
    const buttonsRef = useRef<Array<HTMLButtonElement | null>>([]);

    // حالة Pagination الجديدة
    const [currentPage, setCurrentPage] = useState(1);
    const [eventsPerPage] = useState(6); // عدد الأحداث في كل صفحة

    const filteredEvents = events.filter(event => {
        if (activeFilter === 'all') return true;
        if (activeFilter === 'weekdays') return ['1', '2', '6'].includes(event.id);
        if (activeFilter === 'popular') return ['1', '4'].includes(event.id);
        if (activeFilter === 'latest') return ['3', '5'].includes(event.id);
        if (activeFilter === 'university') return ['4', '5'].includes(event.id);
        if (activeFilter === 'price') return ['1'].includes(event.id);
        return true;
    });

    // حساب الأحداث للصفحة الحالية
    const indexOfLastEvent = currentPage * eventsPerPage;
    const indexOfFirstEvent = indexOfLastEvent - eventsPerPage;
    const currentEvents = filteredEvents.slice(indexOfFirstEvent, indexOfLastEvent);
    const totalPages = Math.ceil(filteredEvents.length / eventsPerPage);

    // تغيير الصفحة
    const paginate = (pageNumber: number) => setCurrentPage(pageNumber);

    useEffect(() => {
        if (isLoading) return;
        if (!isStudent && !isHost && !isAdmin) return;

        const activeIndex = filters.findIndex(f => f.label.toLowerCase() === activeFilter);
        const activeButton = buttonsRef.current[activeIndex];
        if (activeButton && underlineRef.current) {
            underlineRef.current.style.width = `${activeButton.offsetWidth}px`;
            underlineRef.current.style.left = `${activeButton.offsetLeft}px`;
        }

        const fetchEvents = async () => {
            try {
                setIsLoading(true);
                const response = await fetch("", {
                    method: "GET",
                })
                console.log("status : ", response.status)
                const data = await response.json();
                setEvents(data);

                if (!response.ok) {
                    toast({
                        variant: "destructive",
                        title: "خطأ!",
                        description: "فشل في تحميل الحجوزات",
                    });
                }
            } catch (err: any) {
                toast({
                    variant: "destructive",
                    title: "خطأ!",
                    description: "حدث خطأ أثناء جلب البيانات ، تعذر الاتصال بالسيرفر حاول مرة أخرى.",
                });
            } finally {
                setIsLoading(false);
            }
        }
        fetchEvents();
    }, [activeFilter, isStudent, isHost, isAdmin]);

    // إعادة تعيين الصفحة إلى 1 عند تغيير الفلتر
    useEffect(() => {
        setCurrentPage(1);
    }, [activeFilter]);

    return (
        <main className="bg-white min-h-screen relative overflow-hidden">
            <section className="relative flex items-center justify-center h-[60vh] py-20 text-center">
                <div className="absolute inset-0 z-0">
                    <Image
                        src={bgImage}
                        alt="Modern Purple Background"
                        layout="fill"
                        objectFit="cover"
                        className="brightness-75"
                    />
                    <div className="absolute inset-0 bg-gradient-to-br from-purple-900/80 via-indigo-900/60 to-pink-900/40" />
                    <FloatingBlobs />
                </div>

                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: 0.3 }}
                    className="relative z-10"
                >
                    <motion.h1
                        animate={{
                            backgroundPosition: ['0% 50%', '100% 50%', '0% 50%']
                        }}
                        transition={{
                            duration: 6,
                            repeat: Infinity,
                        }}
                        className="text-5xl md:text-7xl font-bold bg-clip-text text-transparent 
                            bg-gradient-to-r from-pink-400 via-purple-400 to-indigo-400 mb-6"
                    >
                        All Events
                    </motion.h1>

                    <motion.p
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        transition={{ delay: 0.8 }}
                        className="text-xl text-purple-100/90 font-light max-w-2xl mx-auto px-4
                            drop-shadow-lg"
                    >
                        Discover curated events blending education, networking, and entertainment.
                        Explore unique experiences tailored for modern enthusiasts.
                    </motion.p>

                    <motion.div
                        initial={{ scale: 0 }}
                        animate={{ scale: 1 }}
                        className="absolute -top-20 -right-20 opacity-20"
                    >
                        <Sparkles size={200} className="text-cyan-400 animate-pulse" />
                    </motion.div>
                </motion.div>
            </section>

            <div className="max-w-6xl mx-auto px-4 py-10 relative">
                <div className="flex flex-wrap justify-center gap-4 mb-14 relative">
                    {filters.map(({ label, icon: Icon }, index) => {
                        const filterKey = label.toLowerCase() as typeof activeFilter;
                        return (
                            <motion.button
                                key={label}
                                ref={el => buttonsRef.current[index] = el}
                                whileHover={{
                                    scale: 1.05,
                                    background: 'linear-gradient(45deg, #9333ea, #4f46e5)'
                                }}
                                whileTap={{ scale: 0.95 }}
                                onClick={() => setActiveFilter(filterKey)}
                                className={`relative flex items-center gap-3 px-6 py-3 rounded-full
                                            border-2 backdrop-blur-lg transition-all duration-300
                                            ${activeFilter === filterKey
                                        ? 'border-purple-500/30 bg-purple-600/20 text-white'
                                        : 'border-white/10 bg-black/95 text-purple-100 hover:bg-purple-500/20'}`}
                                style={{
                                    boxShadow: activeFilter === filterKey
                                        ? '0 4px 24px -2px rgba(165, 180, 252, 0.3)'
                                        : 'none'
                                }}
                            >
                                <Icon size={20} className="text-cyan-400" />
                                <span className="text-sm font-medium tracking-wide">{label}</span>
                                {activeFilter === filterKey && (
                                    <motion.div
                                        className="absolute -bottom-2 h-1 bg-cyan-400 rounded-full"
                                        initial={{ scaleX: 0 }}
                                        animate={{ scaleX: 1 }}
                                        transition={{ type: 'spring', stiffness: 200 }}
                                    />
                                )}
                            </motion.button>
                        );
                    })}
                </div>
                <motion.div
                    className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8"
                    initial="hidden"
                    animate="visible"
                    variants={{
                        hidden: { opacity: 0 },
                        visible: {
                            opacity: 1,
                            transition: {
                                staggerChildren: 0.1,
                                delayChildren: 0.2
                            }
                        }
                    }}
                >
                    <AnimatePresence>
                        
                  </AnimatePresence>
                </motion.div>

                {/* Pagination Controls */}
                {
                    filteredEvents.length > eventsPerPage && (
                        <div className="flex justify-center mt-12">
                            <nav className="flex items-center gap-2">
                                <button
                                    onClick={() => paginate(Math.max(1, currentPage - 1))}
                                    disabled={currentPage === 1}
                                    className={`p-2 rounded-full ${currentPage === 1 ? 'opacity-50 cursor-not-allowed' : 'hover:bg-purple-500/20'}`}
                                >
                                    <ChevronLeft className="text-purple-400" />
                                </button>

                                {Array.from({ length: totalPages }).map((_, index) => (
                                    <button
                                        key={index}
                                        onClick={() => paginate(index + 1)}
                                        className={`w-10 h-10 rounded-full flex items-center justify-center
                                                ${currentPage === index + 1
                                                ? 'bg-purple-600 text-white'
                                                : 'text-purple-400 hover:bg-purple-500/20'
                                            } `}
                                    >
                                        {index + 1}
                                    </button>
                                ))
                                }

                                <button
                                    onClick={() => paginate(Math.min(totalPages, currentPage + 1))}
                                    disabled={currentPage === totalPages}
                                    className={`p-2 rounded-full ${currentPage === totalPages ? 'opacity-50 cursor-not-allowed' : 'hover:bg-purple-500/20'} `}
                                >
                                    <ChevronRight className="text-purple-400" />
                                </button >
                            </nav >
                        </div >
                    )}
            </div >

            {/* Decorations */}
            < motion.div
                className="fixed top-0 left-0 w-full h-full pointer-events-none z-0"
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
            >
                {
                    [...Array(8)].map((_, i) => (
                        <motion.div
                            key={i}
                            animate={{
                                x: [0, 100, 0],
                                y: [0, 50, 0],
                                rotate: [0, 180, 360]
                            }}
                            transition={{
                                duration: 12 + i * 2,
                                repeat: Infinity,
                                ease: "easeInOut"
                            }}
                            className="absolute text-purple-400/20"
                            style={{
                                left: `${Math.random() * 100}%`,
                                top: `${Math.random() * 100} %`
                            }}
                        >
                            <Stars size={48} />
                        </motion.div >
                    ))}
            </motion.div >
        </main >
    );
}
export default AllEventsPage;